
const UserInfo = ({info}) => {
    return (
        <div>
            <table>
            <tbody>
                <tr>
                    <th>User Info</th>
                    <th>Liquidity</th>
                    <th>Margin</th>
                    <th>XVS Balance</th>
                </tr>
                <tr>
                    <td></td>
                    <td>{info.liquidity}</td>
                    <td>{info.margin}</td>
                    <td>{info.balanceXVS}</td>
                </tr>
            </tbody>
            </table>
        </div>
    )
}

export default UserInfo
