import {useState} from 'react'

const Other = ({onLiquidate, onClaimXVSLp, onClaimXVSTrader}) => {
    const [trader, setTrader] = useState('')

    return (
        <div>
            <div>
                <input type='text' size='60' value={trader} onChange={(e) => setTrader(e.target.value)} />
                <button onClick={() => onLiquidate(trader)}>Liquidate</button>
            </div>
            <br/>
            <div>
                <button onClick={() => onClaimXVSLp()}>Claim XVS as Lp</button>    <button onClick={() => onClaimXVSTrader()}>Claim XVS as Trader</button>
            </div>
        </div>
    )
}

export default Other
