import {useState, useEffect} from 'react'
import Web3 from 'web3'
import abis from './utils/abis'

import PoolInfo from './components/PoolInfo'
import UserInfo from './components/UserInfo'
import TokensInfo from './components/TokensInfo'
import SymbolsInfo from './components/SymbolsInfo'
import Other from './components/Other'

const ONE = new Web3.utils.BN('1000000000000000000')
const ZERO_ADDRESS = '0x0000000000000000000000000000000000000000'
const MAX = '0XFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFF'

function nn(value, unit='ether') {
    return Web3.utils.fromWei(value.toString(), unit)
}

function bb(value, unit='ether') {
    return Web3.utils.toWei(value.toString(), unit)
}

function App() {

    const web3 = new Web3(window.ethereum)

    window.ethereum.on('accountsChanged', async function (accounts) {
        await update()
    })

    const view = new web3.eth.Contract(abis.DeriView, '0x517AfdFEbFD24aAC25371f08B0EF6BC385bBb15a')
    const pool = new web3.eth.Contract(abis.PoolImplementation, '0x499ae4243a8eE32b3D07f46D1e3E6952E0Dcc53f')
    const bTokens = {
        bnb: new web3.eth.Contract(abis.ERC20, '0xae13d989daC2f0dEbFf460aC112a837C89BAa7cd'), // wbnb
        busd: new web3.eth.Contract(abis.ERC20, '0x8301F2213c0eeD49a7E28Ae4c3e91722919B8B47'),
        sxp: new web3.eth.Contract(abis.ERC20, '0x75107940Cf1121232C0559c747A986DEfbc69DA9'),
        ada: new web3.eth.Contract(abis.ERC20, '0xcD34BC54106bd45A04Ed99EBcC2A6a3e70d7210F'),
        cake: new web3.eth.Contract(abis.ERC20, '0xe8bd7cCC165FAEb9b81569B05424771B9A20cbEF'),
        matic: new web3.eth.Contract(abis.ERC20, '0xcfeb0103d4BEfa041EA4c2dACce7B3E83E1aE7E3'),
        aave: new web3.eth.Contract(abis.ERC20, '0x4B7268FC7C727B88c5Fc127D41b491BfAe63e144'),
        tusd: new web3.eth.Contract(abis.ERC20, '0xFeC3A63401Eb9C1476200d7C32c4009Be0154169'),
        trx: new web3.eth.Contract(abis.ERC20, '0x19E7215abF8B2716EE807c9f4b83Af0e7f92653F'),
        btcb: new web3.eth.Contract(abis.ERC20, '0xA808e341e8e723DC6BA0Bb5204Bafc2330d7B8e4'),
        eth: new web3.eth.Contract(abis.ERC20, '0x98f7A83361F7Ac8765CcEBAB1425da6b341958a7'),
        ltc: new web3.eth.Contract(abis.ERC20, '0x969F147B6b8D81f86175de33206A4FD43dF17913'),
        xrp: new web3.eth.Contract(abis.ERC20, '0x3022A32fdAdB4f02281E8Fab33e0A6811237aab0'),
    }
    const vTokens = {
        bnb: new web3.eth.Contract(abis.VBNB, '0x2E7222e51c0f6e98610A1543Aa3836E092CDe62c'),
        busd: new web3.eth.Contract(abis.VBep20, '0x08e0a5575de71037ae36abfafb516595fe68e5e4'),
        sxp: new web3.eth.Contract(abis.VBep20, '0x74469281310195A04840Daf6EdF576F559a3dE80'),
        ada: new web3.eth.Contract(abis.VBep20, '0x37c28de42ba3d22217995d146fc684b2326ede64'),
        cake: new web3.eth.Contract(abis.VBep20, '0xedac03d29ff74b5fdc0cc936f6288312e1459bc6'),
        matic: new web3.eth.Contract(abis.VBep20, '0x3619bddc61189f33365cc572df3a68fb3b316516'),
        aave: new web3.eth.Contract(abis.VBep20, '0x714db6c38a17883964b68a07d56ce331501d9eb6'),
        tusd: new web3.eth.Contract(abis.VBep20, '0x3a00d9b02781f47d033bad62edc55fbf8d083fb0'),
        trx: new web3.eth.Contract(abis.VBep20, '0x369fea97f6fb7510755dca389088d9e2e2819278'),
        btcb: new web3.eth.Contract(abis.VBep20, '0xb6e9322c49fd75a367fcb17b0fcd62c5070ebcbe'),
        eth: new web3.eth.Contract(abis.VBep20, '0x162d005f0fff510e54958cfc5cf32a3180a84aab'),
        ltc: new web3.eth.Contract(abis.VBep20, '0xafc13bc065abee838540823431055d2ea52eba52'),
        xrp: new web3.eth.Contract(abis.VBep20, '0x488ab2826a154da01cc4cc16a8c83d4720d3ca2c'),
    }
    const xvs = new web3.eth.Contract(abis.ERC20, '0xB9e0E753630434d7863528cc73CB7AC638a7c8ff')
    const lToken = new web3.eth.Contract(abis.DToken, '0xDCa6322e02394034870773a7ae6523e5bCbF5196')
    const pToken = new web3.eth.Contract(abis.DToken, '0x535BE91DFc405aC12e94A8Ec7f30B21Ef0D42c34')
    const tokenVault = new web3.eth.Contract(abis.TokenVault, '0x16728A3d98671c6b304510819D9C59A197CCcdC2')

    const [data, setData] = useState({
        account: ZERO_ADDRESS,
        poolInfo: {},
        userInfo: {},
        tokensInfo: {},
        symbolsInfo: {},
    })

    useEffect(() => {
        update()
    }, [])

    const update = async () => {
        const account = Web3.utils.toChecksumAddress((await window.ethereum.request({method: 'eth_requestAccounts'}))[0])
        const info = await view.methods.getData(account).call()
        // console.log(info)

        const poolInfo = {
            balanceBUSD: nn(await bTokens.busd.methods.balanceOf(pool.options.address).call()),
            liquidity: nn(info.pool.liquidity),
            lpsPnl: nn(info.pool.lpsPnl),
            cumulativePnlPerLiquidity: nn(info.pool.cumulativePnlPerLiquidity),
            protocolFeeAccrued: nn(info.pool.protocolFeeAccrued),
        }

        const userInfo = {
            liquidity: nn(info.lpInfo.liquidity),
            margin: nn(new Web3.utils.BN(info.tdInfo.amountB0).add(new Web3.utils.BN(info.tdInfo.borrowLimit))),
            balanceXVS: nn(await xvs.methods.balanceOf(account).call()),
        }

        const tokensInfo = await (async () => {
            const res = {}
            for (const key of Object.keys(bTokens)) {
                res[key] = {}
                res[key].balance = key == 'bnb' ? nn(await web3.eth.getBalance(account)) : nn(await bTokens[key].methods.balanceOf(account).call())
            }
            for (let market of info.lpInfo.markets) {
                const key = market.underlyingSymbol == '' ? 'bnb' : market.underlyingSymbol.toLowerCase()
                if (res[key] === undefined) res[key] = {}
                res[key].balanceLpVault = nn(new Web3.utils.BN(market.vTokenBalance).mul(new Web3.utils.BN(market.exchangeRate)).div(ONE))
            }
            for (let market of info.tdInfo.markets) {
                const key = market.underlyingSymbol == '' ? 'bnb' : market.underlyingSymbol.toLowerCase()
                if (res[key] === undefined) res[key] = {}
                res[key].balanceTdVault = nn(new Web3.utils.BN(market.vTokenBalance).mul(new Web3.utils.BN(market.exchangeRate)).div(ONE))
            }
            return res
        })()

        const symbolsInfo = await (async () => {
            const res = {}
            for (const s of info.symbols) {
                const key = s.symbol
                res[key] = {}
                res[key].netVolume = nn(s.netVolume)
                res[key].netCost = nn(s.netCost)
                res[key].indexPrice = nn(s.indexPrice)
                res[key].fundingTimestamp = s.fundingTimestamp
                res[key].cumulativeFundingPerVolume = nn(s.cumulativeFundingPerVolume)
                res[key].tradersPnl = nn(s.tradersPnl)
                res[key].initialMarginRequired = nn(s.initialMarginRequired)
                res[key].nPositionHolders = s.nPositionHolders

                for (const p of info.tdInfo.positions) {
                    if (p.symbol === key) {
                        res[key].userVolume = nn(p.volume)
                        res[key].userCost = nn(p.cost)
                        res[key].userCumulativeFundingPerVolume = nn(p.cumulativeFundingPerVolume)
                        break
                    }
                }
            }
            return res
        })()

        setData({
            account,
            poolInfo,
            userInfo,
            tokensInfo,
            symbolsInfo,
        })
    }

    const onMint = async (key) => {
        const amounts = {
            busd: 10000,
            sxp: 10000,
            ada: 10000,
            cake: 2000,
            matic: 5000,
            aave: 100,
            tusd: 10000,
            trx: 5000,
            btcb: '0.5',
            eth: 5,
            ltc: 100,
            xrp: 20000,
        }
        await tokenVault.methods.withdraw(bTokens[key].options.address, bb(amounts[key])).send({from: data.account})
        await update()
    }

    const onApprove = async (key) => {
        await bTokens[key].methods.approve(pool.options.address, MAX).send({from: data.account})
    }

    const onAddLiquidity = async (key, amount) => {
        if (key == 'bnb') {
            await pool.methods.addLiquidity(ZERO_ADDRESS, '0', []).send({from: data.account, value: bb(amount)})
        } else {
            await pool.methods.addLiquidity(bTokens[key].options.address, bb(amount), []).send({from: data.account})
        }
        await update()
    }

    const onRemoveLiquidity = async (key, amount) => {
        const address = key == 'bnb' ? ZERO_ADDRESS : bTokens[key].options.address
        await pool.methods.removeLiquidity(address, bb(amount), []).send({from: data.account})
        await update()
    }

    const onAddMargin = async (key, amount) => {
        if (key == 'bnb') {
            await pool.methods.addMargin(ZERO_ADDRESS, '0', []).send({from: data.account, value: bb(amount)})
        } else {
            await pool.methods.addMargin(bTokens[key].options.address, bb(amount), []).send({from: data.account})
        }
        await update()
    }

    const onRemoveMargin = async (key, amount) => {
        const address = key == 'bnb' ? ZERO_ADDRESS : bTokens[key].options.address
        await pool.methods.removeMargin(address, bb(amount), []).send({from: data.account})
        await update()
    }

    const onTrade = async (key, amount) => {
        await pool.methods.trade(key, bb(amount), []).send({from: data.account})
        await update()
    }

    const onLiquidate = async (trader) => {
        const pTokenId = await pToken.methods.getTokenIdOf(trader).call()
        if (pTokenId != 0) {
            await pool.methods.liquidate(pTokenId, []).send({from: data.account})
        }
        await update()
    }

    const onClaimXVSLp = async () => {
        await pool.methods.claimVenusLp(data.account).send({from: data.account})
        await update()
    }

    const onClaimXVSTrader = async () => {
        await pool.methods.claimVenusTrader(data.account).send({from: data.account})
        await update()
    }

    return (
        <div className="App">
            <h1>DeriV3 BSC Testnet (20211221)</h1>
            <ol>
                <li>Pool supports oracleSignature and collectProtocolFee. Currently there is no offchain oracle implemented, so just pass `[]` for oracleSignatures when calling pool functions.</li>
                <li>Option margin calculation is unified for pool/trader with delta/gamma method, the original dynamicMarginRatio is dumped.</li>
                <li>All updates are done through implementation update and no storage is touched, thus all onchain states hold tight from previous version 20211218.</li>
            </ol>
            <PoolInfo info={data.poolInfo} />
            <br/>
            <UserInfo info={data.userInfo} />
            <br/>
            <TokensInfo info={data.tokensInfo} onMint={onMint} onApprove={onApprove} onAddLiquidity={onAddLiquidity} onRemoveLiquidity={onRemoveLiquidity} onAddMargin={onAddMargin} onRemoveMargin={onRemoveMargin}/>
            <br/>
            <SymbolsInfo info={data.symbolsInfo} onTrade={onTrade}/>
            <br/>
            <Other onLiquidate={onLiquidate} onClaimXVSLp={onClaimXVSLp} onClaimXVSTrader={onClaimXVSTrader}/>
        </div>
    )

}

export default App
