import {useState} from 'react'

const TokensInfo = ({info, onMint, onApprove, onAddLiquidity, onRemoveLiquidity, onAddMargin, onRemoveMargin}) => {
    const [data, setData] = useState({})

    const rows = []
    rows.push(
        <tr key='name'>
            {[<th key='1'>Tokens Info</th>].concat(
                Object.keys(info).map((key, i) => {
                    return <th key={key}>{key.toUpperCase()}</th>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='balance'>
            {[<td key='1'>Balance in Wallet</td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}>{info[key].balance}</td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='balanceLpVault'>
            {[<td key='1'>Balance in LpVault (Est.)</td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}>{info[key].balanceLpVault}</td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='balanceTdVault'>
            {[<td key='1'>Balance in TdVault (Est.)</td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}>{info[key].balanceTdVault || 0}</td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='mint'>
            {[<td key='1'></td>].concat(
                Object.keys(info).map((key, i) => {
                    return key != 'bnb' ? <td key={key}><button onClick={() => onMint(key)}>Mint</button></td> : <td key={key}></td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='approve'>
            {[<td key='1'></td>].concat(
                Object.keys(info).map((key, i) => {
                    return key != 'bnb' ? <td key={key}><button onClick={() => onApprove(key)}>Approve</button></td> : <td key={key}></td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='amount' bgcolor='lightgreen'>
            {[<td key='1'>Input Amount</td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}><input type='text' size='10' value={data[key] || 0} onChange={(e) => setData({[key]: e.target.value})}/></td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='addLiquidity'>
            {[<td key='1'></td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}><button onClick={() => onAddLiquidity(key, data[key])}>AddLiquidity</button></td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='removeLiquidity'>
            {[<td key='1'></td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}><button onClick={() => onRemoveLiquidity(key, data[key])}>RemoveLiquidity</button></td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='addMargin'>
            {[<td key='1'></td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}><button onClick={() => onAddMargin(key, data[key])}>AddMargin</button></td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='removeMargin'>
            {[<td key='1'></td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}><button onClick={() => onRemoveMargin(key, data[key])}>RemoveMargin</button></td>
                })
            )}
        </tr>
    )

    return (
        <div>
            <table>
            <tbody>
                {rows}
            </tbody>
            </table>
        </div>
    )
}

export default TokensInfo
