
const PoolInfo = ({info}) => {
    return (
        <div>
            <table>
            <tbody>
                <tr>
                    <th>Pool Info</th>
                    <th>BUSD Balance</th>
                    <th>Liquidity</th>
                    <th>LpsPnl</th>
                    <th>CumulativePnlPerLiquidity</th>
                    <th>ProtocolFeeAccrued</th>
                </tr>
                <tr>
                    <td></td>
                    <td>{info.balanceBUSD}</td>
                    <td>{info.liquidity}</td>
                    <td>{info.lpsPnl}</td>
                    <td>{info.cumulativePnlPerLiquidity}</td>
                    <td>{info.protocolFeeAccrued}</td>
                </tr>
            </tbody>
            </table>
        </div>
    )
}

export default PoolInfo
