import {useState} from 'react'

const SymbolsInfo = ({info, onTrade}) => {
    const [data, setData] = useState({})

    const rows = []
    rows.push(
        <tr key='name'>
            {[<th key='1'>Symbols Info</th>].concat(
                Object.keys(info).map((key, i) => {
                    return <th key={key}>{key}</th>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='netVolume'>
            {[<td key='1'>netVolume</td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}>{info[key].netVolume || 0}</td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='netCost'>
            {[<td key='1'>netCost</td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}>{info[key].netCost || 0}</td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='indexPrice'>
            {[<td key='1'>indexPrice</td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}>{info[key].indexPrice || 0}</td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='fundingTimestamp'>
            {[<td key='1'>fundingTimestamp</td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}>{info[key].fundingTimestamp || 0}</td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='cumulativeFundingPerVolume'>
            {[<td key='1'>cumulativeFundingPerVolume</td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}>{info[key].cumulativeFundingPerVolume || 0}</td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='tradersPnl'>
            {[<td key='1'>tradersPnl</td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}>{info[key].tradersPnl || 0}</td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='initialMarginRequired'>
            {[<td key='1'>initialMarginRequired</td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}>{info[key].initialMarginRequired || 0}</td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='nPositionHolders'>
            {[<td key='1'>nPositionHolders</td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}>{info[key].nPositionHolders || 0}</td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='userVolume' bgcolor='lightyellow'>
            {[<td key='1'>userVolume</td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}>{info[key].userVolume || 0}</td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='userCost' bgcolor='lightyellow'>
            {[<td key='1'>userCost</td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}>{info[key].userCost || 0}</td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='userCumulativeFundingPerVolume' bgcolor='lightyellow'>
            {[<td key='1'>userCumulativeFundingPerVolume</td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}>{info[key].userCumulativeFundingPerVolume || 0}</td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='amount' bgcolor='lightgreen'>
            {[<td key='1'>Input Volume</td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}><input type='text' size='10' value={data[key] || 0} onChange={(e) => setData({[key]: e.target.value})}/></td>
                })
            )}
        </tr>
    )
    rows.push(
        <tr key='trade'>
            {[<td key='1'></td>].concat(
                Object.keys(info).map((key, i) => {
                    return <td key={key}><button onClick={() => onTrade(key, data[key])}>Trade</button></td>
                })
            )}
        </tr>
    )

    return (
        <div>
            <table>
            <tbody>
                {rows}
            </tbody>
            </table>
        </div>
    )
}

export default SymbolsInfo
